// Replace the form section in your contact page with this:
"use client";

import { useState } from "react";
import { toast } from "sonner";
import { Send, ArrowRight } from "lucide-react";

// Add this state and functions inside your Contact component
const [submitting, setSubmitting] = useState(false);
const [errors, setErrors] = useState<Record<string, string>>({});

const handleSubmit = async (e: React.FormEvent) => {
  e.preventDefault();
  
  // Basic validation
  const newErrors: Record<string, string> = {};
  if (!formData.name) newErrors.name = "Name is required";
  if (!formData.email) newErrors.email = "Email is required";
  if (!formData.service) newErrors.service = "Service is required";
  
  if (Object.keys(newErrors).length > 0) {
    setErrors(newErrors);
    toast.error("Please fill in all required fields");
    return;
  }

  setSubmitting(true);

  try {
    const formDataToSend = new FormData();
    formDataToSend.append("name", formData.name);
    formDataToSend.append("email", formData.email);
    formDataToSend.append("phone", formData.phone);
    formDataToSend.append("service", formData.service);
    formDataToSend.append("passengers", formData.passengers);
    formDataToSend.append("date", formData.date);
    formDataToSend.append("message", formData.message);
    formDataToSend.append("_subject", "New Contact Form Submission - Belgium Buses");

    const response = await fetch("https://formspree.io/f/myzlwaoy", {
      method: "POST",
      body: formDataToSend,
      headers: {
        Accept: "application/json",
      },
    });

    if (response.ok) {
      toast.success("Thank you! We will get back to you within 2 hours.");
      
      // Reset form
      setFormData({
        name: "",
        email: "",
        phone: "",
        service: "",
        passengers: "",
        date: "",
        message: ""
      });
      setErrors({});
    } else {
      toast.error("Something went wrong. Please try again.");
    }
  } catch (error) {
    console.error("Form submission error:", error);
    toast.error("Network error. Please check your connection and try again.");
  } finally {
    setSubmitting(false);
  }
};

const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
  const { name, value } = e.target;
  setFormData(prev => ({
    ...prev,
    [name]: value
  }));
  
  // Clear error when user starts typing
  if (errors[name]) {
    setErrors(prev => ({
      ...prev,
      [name]: ""
    }));
  }
};

// Then replace your form JSX with this:
<form onSubmit={handleSubmit} className="space-y-6">
  <div className="grid grid-cols-1 sm:grid-cols-2 gap-6">
    <div>
      <label className="block text-sm font-medium text-gray-700 mb-2">
        Full Name *
      </label>
      <input
        type="text"
        name="name"
        required
        value={formData.name}
        onChange={handleChange}
        className={`w-full px-4 py-3 border rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-all duration-300 ${
          errors.name ? 'border-red-500' : 'border-gray-300'
        }`}
        placeholder="Your full name"
      />
      {errors.name && (
        <p className="text-red-500 text-sm mt-1">{errors.name}</p>
      )}
    </div>
    <div>
      <label className="block text-sm font-medium text-gray-700 mb-2">
        Email Address *
      </label>
      <input
        type="email"
        name="email"
        required
        value={formData.email}
        onChange={handleChange}
        className={`w-full px-4 py-3 border rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-all duration-300 ${
          errors.email ? 'border-red-500' : 'border-gray-300'
        }`}
        placeholder="your@email.com"
      />
      {errors.email && (
        <p className="text-red-500 text-sm mt-1">{errors.email}</p>
      )}
    </div>
  </div>

  <div className="grid grid-cols-1 sm:grid-cols-2 gap-6">
    <div>
      <label className="block text-sm font-medium text-gray-700 mb-2">
        Phone Number
      </label>
      <input
        type="tel"
        name="phone"
        value={formData.phone}
        onChange={handleChange}
        className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-all duration-300"
        placeholder="+32 XXX XXX XXX"
      />
    </div>
    <div>
      <label className="block text-sm font-medium text-gray-700 mb-2">
        Service Needed *
      </label>
      <select
        name="service"
        required
        value={formData.service}
        onChange={handleChange}
        className={`w-full px-4 py-3 border rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-all duration-300 ${
          errors.service ? 'border-red-500' : 'border-gray-300'
        }`}
      >
        <option value="">Select a service</option>
        {services.map(service => (
          <option key={service} value={service}>{service}</option>
        ))}
      </select>
      {errors.service && (
        <p className="text-red-500 text-sm mt-1">{errors.service}</p>
      )}
    </div>
  </div>

  <div className="grid grid-cols-1 sm:grid-cols-2 gap-6">
    <div>
      <label className="block text-sm font-medium text-gray-700 mb-2">
        Number of Passengers
      </label>
      <input
        type="number"
        name="passengers"
        value={formData.passengers}
        onChange={handleChange}
        className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-all duration-300"
        placeholder="e.g., 25"
        min="1"
      />
    </div>
    <div>
      <label className="block text-sm font-medium text-gray-700 mb-2">
        Travel Date
      </label>
      <input
        type="date"
        name="date"
        value={formData.date}
        onChange={handleChange}
        className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-all duration-300"
      />
    </div>
  </div>

  <div>
    <label className="block text-sm font-medium text-gray-700 mb-2">
      Message
    </label>
    <textarea
      name="message"
      rows={4}
      value={formData.message}
      onChange={handleChange}
      className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-all duration-300 resize-none"
      placeholder="Tell us about your transportation needs, route details, and any special requirements..."
    />
  </div>

  <button
    type="submit"
    disabled={submitting}
    className="w-full bg-gradient-to-r from-blue-600 to-indigo-600 text-white font-semibold py-4 px-8 rounded-xl hover:shadow-xl transform hover:scale-[1.02] transition-all duration-300 flex items-center justify-center space-x-2 disabled:opacity-50 disabled:cursor-not-allowed group"
  >
    {submitting ? (
      <>
        <div className="w-5 h-5 border-2 border-white border-t-transparent rounded-full animate-spin mr-2"></div>
        Submitting...
      </>
    ) : (
      <>
        Get Free Quote
        <ArrowRight className="h-5 w-5 ml-2 group-hover:translate-x-1 transition-transform" />
      </>
    )}
  </button>
</form>